
.fun-list {
  margin: 0 22px 0 23px;
}

.fun-list__item {
  padding-bottom: 20px;
}

.fun-list__item:not(:last-child) .fun-list__country-wrapper {
    &::after {
      content: "";
      position: absolute;
      top: 9px;
      left: -14px;
      height: 285px;
      width: 2px;
      background-color: #1d2e5b;
    }
}

.fun-list__country-wrapper {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-bottom: 9px;
  &::before {
    content: "";
    position: absolute;
    width: 11px;
    height: 2px;
    background-color: #1d2e5b;
    top: 9px;
    left: -12px;
  }
}

.fun-list__country {
  font-family: "BloggersansM",  Arial, sans-serif;
  font-size: 18px;
  line-height: 18px;
  color: #1d2e5b;
  position: relative;
    &::before {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 6px;
    height: 6px;
    background-color: #1d2e5b;
    top: 7px;
    left: -1px;
  }
}

.fun-list__country--bosnia {
  padding: 0 0 0 11px;
}

.fun-list__country-flag {
  position: relative;
  &::after {
    content: "";
    position: absolute;
    width: 35px;
    height: 24px;
    background-size: cover;
    right: 3px;
    top: 0;
  }

}

.fun-list__country-flag--bosnia {
  &::after {
    background-image: url("../img/flags/Bosnia.png");
  }
}

.fun-list__country--czechia {
  padding: 0 0 0 11px;
}

.fun-list__country-flag--czechia {
   &::after {
   background-image: url("../img/flags/Czechia.png");

   }
}

.fun-list__country-point {
  position: relative;
  &::before {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 14px;
    height: 14px;
    background-color: #1d2e5b;
    top: 7px;
    left: -1px;
    display: none;
  }

}

.fun-list__country-point:not(:last-child) {
    &::after {
      content: "";
      position: absolute;
      top: 9px;
      left: -14px;
      height: 285px;
      width: 2px;
      background-color: #1d2e5b;
      display: none;
    }
}


.fun-list__description {
  font-family: "Bloggersans", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: rgba(68, 68, 68, 0.3);
  width: 100%;
  min-height: 220px;
  resize: none;
  border: 1px solid #cbced9;
  border-radius: 4px;
  box-sizing: border-box;
  cursor: pointer;
  outline: none;
  margin-top: 10px;
  padding: 8px 10px 0 14px;
}

.fun-list__description:hover,
.fun-list__description:focus {
  border-color: #959bb2;
}

.fun-list__description:active {
  border-color: #161c35;
}

