@media(min-width: $tablet) {
  .fun-list__country {
    font-family: "BloggersansB", Arial, sans-serif;
    font-size: 30px;
    line-height: 30px;
    padding: 0;
    &::before {
      content: "";
      position: absolute;
      border-radius: 50%;
      width: 0;
      height: 0;
      background-color: transparent;
      top: 0;
      left: 0;
    }
  }

  .fun-list__item {
    padding: 2px 49px 43px 23px;
  }

  .fun-list__item:not(:last-child) .fun-list__country-wrapper {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 0;
      width: 0;
      background-color: transparent;
    }
}

  .fun-list__country-wrapper {
      &::before {
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        background-color: transparent;
        top: 0;
        left: 0;
      }
  }

  .fun-list__country:not(:last-child) .fun-list__country-wrapper::after {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 0;
      width: 0;
      background-color: transparent;
    }
  }

  .fun-list__country-flag::after {
    width: 70px;
    height: 47px;
    right: 3px;
    top: -7px;
  }

  .fun-list__country-point {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      border-radius: 50%;
      width: 14px;
      height: 14px;
      background-color: #1d2e5b;
      top: 7px;
      left: -1px;
      display: block;
    }

  }

  .fun-list__country-point {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 9px;
      left: -14px;
      height: 285px;
      width: 2px;
      background-color: #1d2e5b;
      display: block;
    }
  }

  // не могу понять как сделать палку отменяемой и чтоб она тянулась + переполняемость к чему пристегнуть


  .fun-list__description {
    font-size: 20px;
    line-height: 23px;
    min-height: 140px;
    margin-top: 31px;
    padding: 16px 33px 0 17px;
  }

}
